"use client";

import {useRouter} from "next/navigation";
import Image from "next/image";
import Link from "next/link";

export default function NavBar() {

    return (

        <header className="flex justify-between items-center py-4 px-6 border-b border-mid-gray rounded-lg">
            <div className="flex items-center space-x-2">
                <svg className="h-6 w-6 text-purple-600" fill="currentColor" viewBox="0 0 24 24"
                     xmlns="http://www.w3.org/2000/svg">
                    <path
                        d="M12 2C6.477 2 2 6.477 2 12s4.477 10 10 10 10-4.477 10-10S17.523 2 12 2zm0 18a8 8 0 100-16 8 8 0 000 16zM15 12a3 3 0 11-6 0 3 3 0 016 0z"/>
                </svg>
                <span className="text-xl font-bold text-white">Nebula</span>
            </div>

            <nav className="space-x-6 hidden sm:flex">
                <Link href="#hero" onClick="window.location.hash=''"
                   className="text-white hover:text-primary-purple transition">Home</Link>
                <a href="#about" onClick="window.location.hash=''"
                   className="text-white hover:text-primary-purple transition">About</a>
                <a href="#works" onClick="window.location.hash=''"
                   className="text-white hover:text-primary-purple transition">Works</a>
                <a href="#contacts" onClick="window.location.hash=''"
                   className="text-white hover:text-primary-purple transition">Contacts</a>
            </nav>

            <a href="#works" onClick="window.location.hash=''"
               className="hidden sm:inline-block px-4 py-2 nebula-gradient text-white font-medium rounded-full shadow-lg hover:shadow-primary-purple/50 transition duration-300">
                View Works
            </a>
        </header>

    )

}